/* src/components/previews/ProjectsPreview/ProjectsPreview.module.scss */

@use 'styles/abstracts' as *;

.projectsPreview {
  padding: var(--spacing-xl) var(--spacing-sm);
  text-align: center;
  margin-bottom: var(--spacing-md);
  // Overall section fade-in animation
  animation: fadeIn 0.8s ease-in-out;

  @media (min-width: 768px) {
    padding: var(--spacing-xl) var(--spacing-md);
  }
}

.projectsPreview__container {
  max-width: 1200px;
  margin: 0 auto;
}

.projectsPreview__heading {
  // Animation for the heading
  transform: translateY(-20px);
  opacity: 0;
  animation: slideInUp 0.6s ease-out 0.2s forwards;
}

.projectsPreview__description {
  max-width: 80ch; // Optimal line length for readability
  margin: 0 auto var(--spacing-xs);
  font-family: var(--font-family-primary);
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-regular);
  line-height: 1.6; // Improved readability via WCAG guidelines
  color: var(--color-text-secondary);

  // Animation for the description
  transform: translateY(-20px);
  opacity: 0;
  animation: slideInUp 0.6s ease-out 0.4s forwards;
}

.projectsPreview__grid {
  display: grid;
  // A responsive grid that fits as many columns as possible
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--spacing-lg);
 margin: var(--spacing-lg) 0;
}

.projectsPreview__cta {
  margin-top: var(--spacing-xl);
}


// Keyframe animations for a professional entry
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideInUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInSlideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}