/* src/components/Cards/ContactCard.module.scss */
@use 'styles/abstracts' as *;

.contacts-card {
  border-radius: var(--border-radius-md);
  overflow: hidden;
  /* Ensures no content spills out */
  text-align: center;
}

.contacts-card__link {
  /* Ensure the entire card is a clickable, focusable area */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-lg);
  min-height: 120px;
  /* Ensure sufficient touch target size (WCAG 2.5.5) */
  min-width: 150px;

  color: var(--color-link);
  text-decoration: none;
  background-color: var(--color-surface-elevated);
  border: 1px solid var(--color-border-primary);
  border-radius: var(--border-radius-md);

  /* Combined both interactive transitions and theme transitions */
  transition:
    background-color 0.3s ease,
    /* Fast hover background change */
    transform 0.3s ease,
    /* Fast transform lift */
    background-color var(--color-transition),
    /* Smooth theme toggle background */
    border-color var(--color-transition),
    /* Smooth theme toggle border */
    color var(--color-transition),
    /* Smooth theme toggle link color */
    box-shadow var(--color-transition);
  /* Smooth theme toggle shadow */

  &:hover,
  &:focus {
    background-color: var(--color-surface-base);
    transform: translateY(-5px);
    box-shadow: var(--box-shadow-md);
    outline: 2px solid var(--color-brand-primary);
    /* Visible focus indicator (WCAG 2.4.7) */
    outline-offset: 1px;
  }

  &:focus-visible {
    outline: 2px solid var(--color-brand-primary);
    /* Visible focus indicator (WCAG 2.4.7) */
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
  }
}

.contacts-card__icon-container {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: var(--font-size-h2);
  height: var(--font-size-h2);
}

.contacts-card__icon {
  /* Style for the SVG or font icon */
  width: 100%;
  height: 100%;
  max-width: var(--font-size-h2);
  max-height: var(--font-size-h2);
  color: var(--color-brand-primary);
  transition: color var(--color-transition), fill var(--color-transition);
}

.contacts-card__name {
  font-family: var(--font-family-primary);
  font-weight: var(--font-weight-bold);
  font-size: var(--font-size-base);
  color: var(--color-text-primary);
  transition: color var(--color-transition);
}